
import { defineComponent } from "vue"; 
import LangSwitcher from "../LangSwitcher/LangSwitcher.vue";
import { getCurrentHomeViewConfig } from "@/utils/config-helper";
import { getDetailViewImgFrom } from "@/views/DetailView/util-helper/config-asset";
import { currentLangId } from "@/utils/lang-helper";


export default defineComponent({
  name: "PageTopHeader",
  components: {
    LangSwitcher,
  },
  props: {
    isShowLeft: {
      type: Boolean,
      default: () => {
        return true;
      },
    },
  },
  setup() {
    return {
      currentLangId,
    };
  },
  data: () => {
    return {};
  },
  computed: {
    logoSrc() {
      return getDetailViewImgFrom(getCurrentHomeViewConfig()?.appScope.logo);
    },
  },
  methods: {
    onClickLeft() {
      this.$router.push({
        path: "/",
      });
    },
    onMouseWheel(evt: MouseEvent) {
      this.$emit("mouse-wheel", evt);
    },
  },
});

